import React, { useState } from "react";
import { Link } from "react-router-dom";
import { RiMenu3Line, RiCloseLine } from "react-icons/ri";
import "./style.css";

const Menu = () => (
  <>
    <Link to={"/pemerintahan"}>Pemerintahan Desa</Link>
    <br />
    <Link to={"/being"}>Informasi Publik</Link>
    <br />
    <Link to={"/kontak"}>Kontak Darurat</Link>
    <br />
    <Link to={"/data"}>Data Desa</Link>
  </>
);
const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);
  return (
    <div className="web__navbar">
      <div className="web__navbar-logo">
        <Link to={"/"}>
          <h1>Sukanegeri Jaya</h1>
        </Link>
      </div>
      <div className="web__navbar-links">
        <Menu />
        <Link to={"/login"}>
          <button>Log In</button>
        </Link>
      </div>
      <div className="nav__responsive">
        <div className="nav__side">
          {toggleMenu ? (
            <RiCloseLine
              color="white"
              size={27}
              onClick={() => setToggleMenu(false)}
            />
          ) : (
            <RiMenu3Line
              color="white"
              size={27}
              onClick={() => setToggleMenu(true)}
            />
          )}
          {toggleMenu && (
            <div className="web__navbar-menu_container">
              <div className="web__navbar-links__container">
                <Menu />
              </div>
              <div className="nav__login">
                <Link to={"/login"}>
                  <button>Log In</button>
                </Link>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Navbar;
